.navbar {
    margin-top: .5em;
    position: fixed;
    top: 0;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
    width: 45em;
    height: 3em;
    border-radius: 3.1em;
    background: #6A725D;
    box-shadow: .3em .3em .5em 0em rgba(0, 0, 0, 0.40);
    display: flex;
    justify-content: center;
  }
  
  a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 0 .75em;
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  
  .navbar a + a {
    margin-left: 3em;
  }
  
  .navbar a:hover {
    /* background-color: #8e9687; */
    position: relative;
  }
  
  .navbar a:hover::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 2.5em;
    height: 2.5em;
    border-radius: 4em;
    padding-left: 2.5em;
    padding-right: 2.5em;
    background-color: rgba(255, 255, 255, 0.5);
    pointer-events: none; 
  }

  .iconWhite{
    height: 1.75em;
    width: auto;
  }

@media screen and (max-width: 800px) {
  .navbar{
    width:100vh;
    height: 3.5em;
    margin: 0;
  }
}

@media screen and (max-width: 800px) {
  .navbar a{
    display: inline-flex;
    justify-content: space-evenly;
    align-items: auto;
    height: 100%;
    padding: 0;
    margin: 0;
    font-size: .8em;
  }
}

@media screen and (max-width: 800px) {
  .navbar a:hover::before{
    width: 1em;
    border-radius: 1em;
  }
}

@media screen and (max-width: 800px) {
  a{
    padding-left: .25em !important;
  }
}