.flowerRow{
    height: 6vh;
    width: auto;
    margin: 0;
    padding: 0;
    background-size:contain;
    background-color: #929A85;
  }

@media screen and (max-width: 800px) {
  .flowerRow{
    padding-top: 7.5em;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
}

@media screen and (max-width: 800px) {
  .flowerRow img{
    height: auto;
  }
}
